import axios from 'axios'

function cleanAuthorizationResponse(accessToken) {
    return accessToken.replace(/Bearer\s/ig, '')
}

function addAuthorizationHeader(accessToken) {
    return axios.defaults.headers.common['Authorization'] = "Bearer " +  accessToken
}

export { addAuthorizationHeader, cleanAuthorizationResponse };


